.footer {
  width: 100%;
  padding: 2rem 0;
  background-color: #333;
  color: #fff;
}

.footer .top,
.bottom {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.footer .social {
  display: flex;
  justify-content: space-between;
  width: 280px;
}

.footer .icon {
  color: var(--primary-light);
}
.footer .icons {
  color: var(--red);
}

.footer .icon:hover,
.icons:hover {
  transform: scale(1.3);
  transition: all ease-in-out 0.6s;
}

.footer .left,
.right {
  display: flex;
  align-items: center;
}

.footer ul {
  display: flex;
}

.footer li {
  padding: 0.5rem;
}

@media (max-width: 1140px) {
  .footer ul {
    flex-direction: column;
  }
}
